html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* Global */
body {
  background-color: #141414;
  color: #ffffff;
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
}

.container {
  max-width: 130rem;
  width: 95%;
  margin: 0 auto;
  margin-inline: auto;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
  font-family: "Raleway", sans-serif;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
  color: #ffffff;
  margin: 0;
}

h3 {
  font-size: 3.2rem;
}

p {
  font-size: 1.6rem;
  line-height: 2;
  color: #ffffff;
  margin: 0;
}

a {
  text-decoration: none;
  color: #ffffff;
}

li {
  list-style: none;
}

ul {
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
  /* para que no se muestre con un espacio abajo */
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.6rem 4rem;
}
.header__links {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__logo, .header__language {
  cursor: pointer;
}
.header__link {
  padding: 0.8rem 1.5rem 8px 1.5rem;
}
.header__link:hover a {
  color: #fe9a9d;
}

.main__grid {
  height: 100rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(6, 1fr);
  gap: 2rem;
}

.block {
  background-color: #0d0d0e;
  border-radius: 1rem;
}
.block--about {
  display: flex;
  justify-content: space-between;
}
.block--about img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 0 1rem 1rem 0;
}
.block__content {
  padding: 3.2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-self: start;
}
.block__social {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  gap: 4rem;
}
.block__social a {
  transition: color 0.3s ease-in-out;
}
.block__social a:hover {
  color: #fe9a9d;
}
.block__social i {
  font-size: 5rem;
}
.block__heading {
  margin-bottom: 2rem;
}
.block:nth-child(1) {
  grid-column: span 2;
  grid-row: span 2;
}
.block:nth-child(2) {
  grid-column: 3/4;
  grid-row: 1/3;
}
.block:nth-child(2) img {
  width: 100%;
  height: 32rem;
  object-fit: cover;
  border-radius: 1rem;
}
.block:nth-child(3) {
  display: flex;
  align-items: center;
  justify-content: center;
}
.block:nth-child(3) p {
  font-size: 3.2rem;
}
.block:nth-child(4) {
  grid-column: 1/2;
  grid-row: span 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 8rem 3rem;
}
.block:nth-child(4) p {
  font-size: 2.5rem;
}
.block:nth-child(5) {
  grid-column: 2/4;
  grid-row: span 2;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 4rem 3rem;
}
.block:nth-child(5) h3 {
  margin: 0;
}
.block:nth-child(5) .icons {
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.block:nth-child(5) .icons i {
  font-size: 5rem;
}
.block:nth-child(6) {
  grid-column: 4/5;
  grid-row: 2/span 3;
}
.block:nth-child(6) img {
  width: 100%;
  height: 49rem;
  object-fit: cover;
  border-radius: 1rem;
}
.block:nth-child(7) {
  grid-row: span 2;
  grid-column: span 2;
}
.block:nth-child(7) img {
  width: 100%;
  height: 32rem;
  object-fit: cover;
  border-radius: 1rem;
}
.block:nth-child(8) {
  display: flex;
  align-items: center;
  justify-content: center;
}
.block:nth-child(8) p {
  font-size: 3rem;
}
.block:nth-child(9) {
  grid-row: 6/7;
  grid-column: 3/4;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.block:nth-child(9) p {
  font-size: 2rem;
}
.block:nth-child(10) {
  grid-row: span 2;
}

.text-gradient {
  background: linear-gradient(90deg, #FE9A9D 0%, #D798D9 50%, #B1D3F9 100%);
  font-weight: bold;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.text-mevn {
  background: linear-gradient(90deg, #42D392 0%, #647EFF 100%);
  font-weight: bold;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}